.host {
    display: flex;
    align-items: center;
    justify-content: flex-end;
    gap: 15px;
    img {
        border-radius: 100%;
        width: 30%;
    }
    
}
.host__name {
    margin: 0;
    color: #FF6060;
    font-size: 18px;
    text-align: right;
    width: 30%;
    line-height: 26px;
}
@media screen and (max-width: 769px) {
    .host {
        img {
            width: 50px;
        }

        &__name {
            width: 100%;
        }
    }
}

@media screen and (min-width: 769px) and (max-width: 991px) {
    .host {
        &__name {
            width: 100%;
        }
    }
}